<template>
  <div class="container">
    <h4>graph to come</h4>

    <div class="sprint-table">

    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import {FETCH_SPRINTS} from "@/store/actions"

export default {
  sname: "Sprints",
  data () {
    return {
      sprints: []
    }
  },
  methods: {
    ...mapActions([
      FETCH_SPRINTS
    ])
  },
  created () {
    this.fetchSprints()
  }
}
</script>

<style scoped>

</style>



