import { SprintsApi } from '@/api/api.service'
import {
  FETCH_SPRINTS
} from '@/store/actions'
import {
  SET_SPRINTS
} from '@/store/mutations'

const state = {
  sprints: []
}

const getters = {
  getSprints (state) {
    return state.getSprints
  }
}

const actions = {
  async [FETCH_SPRINTS] ({ commit }, _) {
    const { data } = await SprintsApi.index()
    commit(SET_SPRINTS, data)
  }
}

const mutations = {
  [SET_SPRINTS] (state, sprints) {
    state.sprints = sprints
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



